body {
  margin: auto;
  padding: 0;
  box-sizing: border-box;
  font-family: open sans, san serif;
  max-width: 375px;
  height: 100vh;
  display: grid;
  align-items: center;
}

/* Loading */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.loading {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-family: museo, serif;
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height: 812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 0.75fr 1fr 1fr;
}

#logo {
  grid-row: 3;
  grid-column: 2;
  justify-self: center;
  align-self: start;
  width: 95px;
  height: 95px;
}

.loading h1 {
  grid-row: 3;
  grid-column: 2;
  justify-self: center;
  align-self: center;
  font-size: 2.2rem;
  margin-top: 0;
  color: #f7f7f7;
}

.carrier-info {
  grid-row: 1;
  grid-column: 1 / span 3;
  display: flex;
  justify-content: space-between;
}

.carrier-icon {
  align-self: center;
  margin-left: 30px;
}

.network-icons {
  align-self: center;
  margin-right: 30px;
  width: 70px;
  display: flex;
  justify-content: space-evenly;
}


/* Login */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.login {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-family: museo, serif;
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height: 812px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 50px 0.5fr 1fr 0.5fr;
}


.login h1 {
  grid-row: 3;
  grid-column: 2;
  font-family: open-sans, sans-serif;
  font-weight: lighter;
  align-self: flex-start;
  margin: 0;
  color: #f7f7f7;
  text-align: center;
}

#loginlogo {
  width: 22px;
  height: 22px;
  margin-right: 5px;
}

.login span {
  color: #f7f7f7;
  font-size: 1.25rem;
}

.login-title {
  grid-row: 2;
  grid-column: 2;
  justify-self: center;
  display: flex;
  justify-content: space-between;
  position: relative;
  top: 5px;
}

#welcome {
  grid-row: 3;
  grid-column: 2;
  align-self: flex-start;
  justify-self: center;
  position: relative;
  top: 45px;
  width: 75px;
}

.login form {
  grid-row: 3;
  grid-column: 2;
  align-self: flex-end;
  width: 250px;
  height: 260px;
  background-color: #fff;
  border-radius: 5px;
  padding: 10px;
  position: relative;
}

.login input {
  height: 40px;
  width: 97%;
  margin-bottom: 5px;
  border: none;
  border-bottom: 1px solid #DFE0E1;    
}

.login label {
  color: #737373;
  font-family: open-sans, sans-serif;
  font-weight: lighter;
  font-size: 11px;
}

#remember {
  width: 15px;
  height: 12px;
  margin: 0;
  position: relative;
  top: 12px;
  left: 3px;
}

#remember-me {
  position: relative;
  top: 10px;
}

.login button {
  margin: auto;
  display: block;
  background-color: #F08A4B;
  color: #f7f7f7;
  position: relative;
  top: 30px;
  height: 40px;
  width: 100%;
  border: none;
  border-radius: 5px;
  outline: none;
  font-family: open-sans, sans-serif;
  font-weight: lighter;
  font-size: 16px;
}

.login p {
  margin: 0;
  color: #F08A4B;
  position: absolute;
  bottom: 15px;
  left: 52px;
  font-family: open-sans, sans-serif;
  font-weight: lighter;
  font-size: 11px;
}

/* Accounts */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.account {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height:  812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 120px 65px 70px 65px 65px 70px 65px 1fr;
  position: relative;
}

.account h5 {
  grid-row: 2;
  grid-column: 2;
  color: #f7f7f7;
  font-weight: 100;
  align-self: flex-start;
  margin-top: 0;
  justify-self: center;
}

#account-title {
  grid-row: 2;
  grid-column: 2;
  justify-self: center;
  position: relative;
  top: 20px;
}

.account-banner {
  grid-row: 2;
  grid-column: 1 /span 3;
  background-color: #C6D6F5;
  color: #2F5296;
  font-weight: 300;
  font-size: 13px;
  padding: 0 15px;
  border-bottom: 1px solid #2F5296;
  height: 70px;
  position: relative;
  top: 50px;
 
}

.account-banner p {
  position: relative;
  top: 2px;
}

.account .scheduled-transactions {
  grid-row: 3;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  border-bottom: 2px solid #F08A4B;
  padding: 0 15px;
}

.scheduled-transactions h3 {
  font-size: 14px;
  margin-bottom: 0;
  color: #202020;
}

.scheduled-transactions p {
  font-size: 13px;
  font-weight: 100;
  margin-top: 3px;
  margin-bottom: 0;
}

.account .checking-savings {
  grid-row: 4;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding: 0 15px;
  border-bottom: 1px solid #d0d0d0;
  display: flex;
  justify-content: space-between;
  
}


.checking-savings span {
  font-size: 14px;
  font-weight: lighter;
  align-self: center;
}

.checking-savings p {
  font-size: 11px;
  font-weight: lighter;
  align-self: center;
  margin: 0;
}

.checking-savings h3 {
  font-size: 20px;
  align-self: center;
  margin: 0;
  color: #202020;
}


.account .account-checking {
  grid-row: 5;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding: 0 15px;
  border-bottom: 1px solid #d0d0d0;
  display: flex;
  justify-content: space-between;
}

.account-checking span {
  font-size: 20px;
  align-self: center;
  color: #202020;
}

.account-checking p {
  font-size: 11px;
  font-weight: lighter;
  align-self: center;
  margin: 0;
}

.account-checking h3 {
  font-size: 14px;
  color: #202020;
  align-self: center;
  margin: 0;
}

.account .account-savings {
  grid-row: 6;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding: 0 15px;
  border-bottom: 2px solid #F08A4B;
  display: flex;
  justify-content: space-between;
}

.account-savings span {
  font-size: 20px;
  align-self: center;
  color: #202020;
}

.account-savings p {
  font-size: 11px;
  font-weight: lighter;
  align-self: center;
  margin: 0;
}

.account-savings h3 {
  font-size: 14px;
  color: #202020;
  align-self: center;
  margin: 0;
}

.account .loans-and-credit {
  grid-row: 7;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding: 0 15px;
  border-bottom: 1px solid #d0d0d0;
  display: flex;
  justify-content: space-between;
}

.loans-and-credit span {
  font-size: 14px;
  font-weight: lighter;
  align-self: center;
  
}

.loans-and-credit p {
  font-size: 11px;
  font-weight: lighter;
  align-self: center;
  margin: 0;
}

.loans-and-credit h3 {
  font-size: 20px;
  align-self: center;
  margin: 0;
  color: #202020;
}

.account .account-consolidation {
  grid-row: 8;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding: 0 15px;
  border-bottom: 1px solid #d0d0d0;
  display: flex;
  justify-content: space-between;
}

.account-consolidation span {
  font-size: 20px;
  align-self: center;
}

.account-consolidation p {
  font-size: 11px;
  font-weight: lighter;
  align-self: center;
  margin: 0;
}

.account-consolidation h3 {
  font-size: 14px;
  color: #202020;
  align-self: center;
  margin: 0;
}

.account .account-edit-view {
  grid-row: 9;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding: 0 15px;
}

.account-edit-view p {
  font-size: 14px;
  font-weight: lighter;
  color: #737373;
}

.account-edit-view span {
  font-size: 14px;
  font-weight: lighter;
  color: #103783;
  position: relative;
  bottom: 5px;
  left: 5px;
}

.total-cash {
  align-self: center;
  color: #202020;
}


.bottom-nav {
  width: 375px;
  background-color: #f7f7f7;
  display: flex;
  font-size: 12px;
  justify-content: space-evenly;
  align-self: flex-end;
  text-align: center;
  border-top: 1px solid #d0d0d0;
  padding-top: 15px;
  padding-bottom: 30px;
  position: absolute;
  bottom: 0;
}

.bottom-nav p {
  margin: 0;
  color: #737373;
  font-weight: lighter;
}


/* Transfer */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.transfer {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height:  812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 50px .5fr .1fr 1.9fr;
}

.transfer-title {
  grid-row: 2;
  grid-column: 1 /span 3;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.transfer-title h5 {
  margin: 0;
  align-self: center;
  color: #f7f7f7;
  font-size: 14px;
  font-weight: lighter;
}

.transfer-title a {
  align-self: center;
}

.transfer-actions {
  grid-row: 3;
  grid-column: 1 /span 3;
  background-color: #F4F3F9;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.view-deposits {
 width: 95px;
 height: 85px;
 background-color: #ffffff;
 text-align: center;
 border-radius: 5px;
}

.view-deposits img {
  height: 26px;
  width: 20px;
  margin-top: 10px;
}

.view-deposits p {
 margin: 0;
 color: #103783;
 font-size: 12px;
 font-weight: lighter;
}

.make-transfer {
  width: 100px;
 height: 85px;
 background-color: #F08A4B;
 text-align: center;
 border-radius: 5px;
}

.make-transfer img {
  margin-top: 8px;
}

.make-transfer p {
  margin: 0;
  color: #ffffff;
  font-size: 12px;
  font-weight: lighter;
}

.scheduled-title {
  grid-row: 4;
  grid-column: 1 /span 3;
  color: #202020;
  font-weight: lighter;
  height: 40px;
  background-color: #DFE0E1;
  display: flex;
  align-items: center;
  padding: 0 15px;
}

.scheduled-title p {
  margin: 0;
  font-size: 14px;
}

.scheduled-transfer {
  grid-row: 5;
  grid-column: 1 /span 3;
  padding: 0 15px;
  background-color: #f7f7f7;
  color: #737373;
}

