  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height:  812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 50px .5fr .1fr 1.9fr;
}

.bill-title {
  grid-row: 2;
  grid-column: 1 /span 3;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.bill-title h5 {
  margin: 0;
  align-self: center;
  color: #f7f7f7;
  font-size: 14px;
  font-weight: lighter;
}

.bill-actions {
  grid-row: 3;
  grid-column: 1 /span 3;
  background-color: #F4F3F9;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.view-deposits {
 width: 95px;
 height: 85px;
 background-color: #ffffff;
 text-align: center;
 border-radius: 5px;
}

.view-deposits img {
  height: 26px;
  width: 20px;
  margin-top: 10px;
}

.view-deposits p {
 margin: 0;
 color: #103783;
 font-size: 12px;
 font-weight: lighter;
}

.make-payment {
  width: 100px;
 height: 85px;
 background-color: #F08A4B;
 text-align: center;
 border-radius: 5px;
}

.make-payment img {
  margin-top: 5px;
}

.make-payment p {
  margin: 0;
