  font-size: 14px;
}

.current-bills {
  grid-row: 5;
  grid-column: 1 /span 3;
  padding: 0 15px;
  background-color: #f7f7f7;
  color: #737373;
}

.current-bills p {
  margin: 5px 0;
  font-weight: lighter;
}

.current-bills span {
  color: #103783;
  font-size: 14px;
  font-weight: lighter;
}


/* Deposit */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.deposit {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height:  812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 50px .2fr 1.8fr;
}

.deposit-title {
  grid-row: 2;
  grid-column: 1 /span 3;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.deposit-title h5 {
  margin: 0;
  align-self: center;
  color: #f7f7f7;
  font-size: 14px;
  font-weight: lighter;
}

.deposit-action {
  grid-row: 3;
  grid-column: 1 /span 3;
  background-color: #C6D6F5;
  display: flex;
  justify-content: center;
}

.deposit-history {
  grid-row: 4;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  display: flex;
  justify-content: center;
}

.deposit-container {
  width: 300px;
  height: 70px;
  border: 1px solid #d0d0d0;
  border-radius: 5px;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 5px;
  margin-top: 30px;
}

.deposit-container p {
  margin: 0;
  color: #103783;
  font-weight: lighter;
}


/* Help */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.help {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height: 812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 50px 1fr;
}

.help-title {
  grid-row: 2;
  grid-column: 1 /span 3;
  display: flex;
  justify-content: center;
  padding: 0 15px;
}

.help-title h5 {
  margin: 0;
  align-self: center;
  color: #f7f7f7;
  font-size: 14px;
  font-weight: lighter;
}

.help-menu {
  background-color: #f7f7f7;
  grid-row: 3;
  grid-column: 1 /span 3;
  padding: 15px 15px;
  color: #737373;
  font-weight: lighter;
}

.help-item {
  margin: 15px 0;
  padding-bottom: 15px;
  border-bottom: 1px solid #d0d0d0;
  display: flex;
  align-items: center;
}

.help-item span {
  position: relative;
  left: 12px;
}

#check {
  color: green;
}


/* Loan */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.loan {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height:  812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 50px .6fr .45fr .25fr 1.8fr
}

.loan-title {
  grid-row: 2;
  grid-column: 1 /span 3;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.loan-amount {
  grid-row: 3;
  grid-column: 1 /span 3;
  text-align: center;
  background-color: #103783;
  color: #f7f7f7;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.loan-amount span {
  font-size: 11px;
  font-weight: 300;
  color: #d0d0d0;
  position: relative;
  top: 13px;
}

.loan-amount h1 {
  font-size: 42px;
  font-weight: lighter;
  margin: 2px;
}

.loan-amount p {
  font-size: 14px;
  font-weight: 300;
  margin-bottom: 0;
  color: #7EC8FD;
  position: relative;
  top: 5px;
}



.loan-title h5 {
  margin: 0;
  align-self: center;
  color: #f7f7f7;
  font-size: 16px;
  font-weight: lighter;
}

.loan-actions {
  grid-row: 4;
  grid-column: 1 /span 3;
  background-color: #C6D6F5;
  padding: 0 15px;
}

.pay-loan {
  text-align: center;
}

.pay-loan p {
  font-size: 12px;
  font-weight: lighter;
  color: #737373;
}

.pay-loan span {
  font-weight: bold;
  color: #202020;
}

.pay-loan button {
  background-color: #F08A4B;
  color: #f7f7f7;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 13px 0;
}

.search-payment {
  grid-row: 5;
  grid-column: 1 /span 3;
  color: #202020;
  font-weight: lighter;
  background-color: #DFE0E1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}

.search-payment input {
  height: 35px;
  width: 90%;
  border: none;
  border-radius: 5px;
  
}

.search-payment input::placeholder {
  color: #d0d0d0;
  font-weight: bold;
  text-align: center;
}

#search {
  position: absolute;
  left: 20px;
}


.last-payment {
  grid-row: 6;
  grid-column: 1 /span 3;
  padding: 0 15px;
  background-color: #f7f7f7;
  color: #737373;
}

.last-payment h3 {
  color: #2F5296;
  font-size: 24px;
  font-weight: 300;
}

.payment-recieved {
  border-radius: 5px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}

.last-payment p {
  font-size: 14px;
  font-weight: 500;
}

.last-payment span {
  font-size: 14px;
  font-weight: 500;
}


/* Pay Loan */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.payloan {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height:  812px;
  display: grid;
  grid-template-columns: .75fr 1fr 1fr;
  grid-template-rows: 50px 50px .5fr  1.25fr
}

.pay-title {
  grid-row: 2;
  grid-column: 1 /span 3;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.pay-title h5 {
  margin: 0;
  align-self: center;
  color: #f7f7f7;
  font-size: 16px;
  font-weight: lighter;
}

.pay-title {
  align-self: center;
}

.select-amount {
  grid-row: 3;
  grid-column: 1;
  background-color: #7EC8FD;
  color: #202020; 
  padding: 0 15px;
  position: relative;
}

.select-amount p {
  position: absolute;
  top: 30%;
}

.payment-options {
  grid-row: 3;
  grid-column: 2 /span 2;
  background-color: #f7f7f7;
  color: #202020;

}

.payment-options p {
  margin: 3px;
}

.minimum-payment, .today-payoff, .other-payment {
  height: 60px;
  border-bottom: 1.25px solid #DFE0E1;
  position: relative;
  display: flex;
  padding: 5px 0;
}

.payment-radio {
  display: flex;
  align-items: center;
  margin: 0 15px;
}

.pay-confirm {
  grid-row: 4;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding:  15px;
  display: flex;
  flex-direction: column;
}

.pay-confirm button {
  margin: 5px 0;
