/* Checking */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

.checking {
  background: linear-gradient(168.49deg, #103783 55.32%, #2F5296 99.23%);
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height:  812px;
  display: grid;
  grid-template-columns: 1fr 1.25fr 1fr;
  grid-template-rows: 50px 50px .4fr .85fr .35fr 1.5fr;
}

.checking-title {
  grid-row: 2;
  grid-column: 1 /span 3;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.checking-title h5 {
  margin: 0;
  align-self: center;
  color: #f7f7f7;
  font-size: 16px;
  font-weight: lighter;
}

.checking-amount {
  position: relative;
  grid-row: 3;
  grid-column: 1 /span 3;
  text-align: center;
  background-color: #103783;
  color: #f7f7f7;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.checking-amount span {
  font-size: 12px;
  font-weight: 300;
  color: #f7f7f7;
  position: relative;
  top: 13px;
}

.checking-amount h1 {
  font-size: 42px;
  font-weight: lighter;
  margin: 2px;
}

.checking-amount p {
  font-size: 14px;
  font-weight: 300;
  margin-bottom: 0;
  color: #7EC8FD;
  position: relative;
  top: 5px;
}



.show-details {
  width: 100%;
  position: absolute;
  top: 137px;
  box-sizing: border-box;
  height: 0px;
  background-color: #08245A;
  display: flex;
  flex-direction: column;
  color: #C6D6F5;
  font-weight: 300;
  padding: 0 15px;
  overflow: hidden;
  transition: height .5s;
  z-index: 3;
}



.detail {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
}

.detail p {
  margin: 0;
  font-size: 14px;
}

.detail span {
  font-size: 14px;
  position: relative;
  top: 0;
}

.comparison-detail {
  display: flex;
  color: #7EC8FD;
}

.comparison-detail p {
  margin: 0;
  margin-right: 5px;
  font-size: 14px;
}

#checking-link {
  color: #7EC8FD;
}

#checking-spacer {
  margin-bottom: 15px;
}

.checking-actions {
  grid-row: 4;
  grid-column: 1 /span 3;
  background-color: #f7f7f7;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.schedule-header {
  display: flex;
  height: 55px;
  background-color: #f7f7f7;
  padding: 0 15px;
  border: 1px solid #d0d0d0;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.schedule-header p {
  font-size: 14px;
  font-weight: 400;
  color: #2F5296;
  margin: 0;
}

.schedule-header span {
  font-size: 13px;
  font-weight: 300;
  color: #737373;
}


.option-header {
  display: flex;
  height: 60px;
  background-color: #f7f7f7;
  display: flex;
  justify-content: space-between;
}

.checking-option {
  box-sizing: border-box;
  height: 55px;
  width: 95px;
  text-align: center;
  border: 1px solid #d0d0d0;
  border-radius: 5px;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.checking-option span {
  font-size: 12px;
  font-weight: 300;
  color: #2F5296;
}

.checking-option img {
  height: 20px;
}

.search-checking {
  grid-row: 5;
  grid-column: 1 /span 3;
  color: #202020;
  font-weight: lighter;
  background-color: #DFE0E1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}

.search-checking input {
  height: 35px;
  width: 90%;
  border: none;
  border-radius: 5px;
  
}

.search-checking input::placeholder {
  color: #d0d0d0;
  font-weight: bold;
  text-align: center;
}

#search {
  position: absolute;
  left: 20px;
}

.last-transaction {
  grid-row: 6;
  grid-column: 1 /span 3;
  padding: 0 15px;
  background-color: #f7f7f7;
  color: #737373;
  overflow: scroll;
}

.last-transaction h3 {
  color: #2F5296;
  font-size: 22px;
  font-weight: 300;
  margin: 13px 0;
}

.pending {
  border-radius: 5px;
  background-color: #eefd9b42;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  margin: 15px 0;
}

.last-transaction p {
  font-size: 14px;
  font-weight: 400;
  color: #202020;
}

.last-transaction span {
  font-size: 14px;
  font-weight: 400;
  color: #202020;
}

